import React, { Component } from 'react';
import './styles/Navbar.css';

import { Link } from 'react-router';

class Navbar extends Component {
	constructor(){
		super();
		this.state = {
			authbtn: <div id="authbtn" className="navbtn">Sign in</div>
		}
	}
	render(){
		return (
			<div>
			<div id="navbar-container">
				<div id="greeting">Votinator</div>
				<div id="divider"></div>
				
				{this.state.authbtn}
				<Link to="/"><div
					id="allpolls"
					className="navbtn"
					onClick={this.props.handleAllPollsClick}
					>
					All Polls
				</div></Link>
				<Link to="/new"><div
					id="newpoll"
					className="navbtn"
					onClick={this.props.handleNewPollClick}
				>
					New poll
				</div></Link>
			</div>
			
			<div id="content">
				{this.props.children}
				</div>
		
		</div>
		)
	}
}

export default Navbar;


